
































// 组件命名严格按照驼峰命名法
// import vertryPic  from '../../../components/vertrypic.vue';
export default {
	data() {
		return {
			vertyText: "获取验证码",
			currentTime: 60,
			disabled: false,
			phoneNumber: '',
			password: '',
			vertyCode: '',
			interval: null,
			// seePassImage: '../../../static/img/viewnoicon.png',
			piccode:'',
			xcode:'',
			focus:0
		};
	},
	computed: {
		correctPhone() {
			return this.$utils.checkMobile(this.phoneNumber);
		}
		
	},
	methods:{
		phoneFocus:function(){
			this.focus = 0;
		},
		vertyPicFocus:function(){
			this.focus = 1;
		},
		vertyCodeFocus:function(){
			this.focus = 2;
		},
		login:function(){
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				}); 
				return;
			}
			if (this.vertyCode == '') {
				uni.showToast({
					title: '请输入验证码',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl+'user_login_quick',
				method: 'POST',
				data: {
					xcode:this.xcode,
					code:this.vertyCode,
					mobile:this.phoneNumber
				},
				success: res => {
					console.log(res);
					if(res.code == 0){
						uni.switchTab({
							url:"../../index/index"
						}) 
					}
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		toPassLogin:function(){
			uni.navigateBack({
				delta:1
			})
		},
		getPhoneNumber: function(e) {
			this.phoneNumber = e.target.value;
			if(this.correctPhone){
				this.piccode = this.$imageUrl+'verify';
			}
		},
		getInputCode: function(e) {
			this.vertyCode = e.target.value;
		},
		getPassWord: function(e) {
			this.password = e.target.value;
		},
		clickPicVerify:function(){
			this.piccode = this.$imageUrl+'verify'+'&random='+Date.now()
		},
		getVertryCode: function() {
			var that = this;
			console.log("this.$webUrl==" + this.$webUrl);
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl + "user_verify_smscode",
				data: {
					mobile: this.phoneNumber
				},
				success: res => {
					that.AppSendVerifyCode(res);
				},
				fail: () => {
		
				},
				complete: () => {
		
				}
			})
		},
		AppSendVerifyCode(res) {
			console.log(JSON.stringify(res));
			var that = this;
			if (res.code == '0') {
				var currentTime = that.currentTime;
				this.interval = setInterval(function() {
					this.disabled = true
					currentTime--;
		
					that.vertyText = currentTime + '秒'
					if (currentTime <= 0) {
						clearInterval(this.interval)
						that.vertyText = '重新发送',
							that.currentTime = 60,
							that.disabled = false
					}
		
				}, 1000)
			} else {
				uni.showModal({
					title: '提示',
					content: res.msg,
					showCancel: false,
					cancelText: '',
					confirmText: '知道了',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
